import {
	getPB08HByMtarea,
	GetPR02WIQTYByPtno,
	getScrcnoMtareaIn
} from '@/api/api.js'
export default {
	data() {
		return {
			heightH: uni.getSystemInfoSync().windowHeight,
			result: "",
			result1: "",
			scanData: new Object(),
			scanData1: new Object(),
			commitFlag: true
		}
	},
	methods: {
		scan: function() {
			uni.scanCode({
				success: (res) => {
					console.log(JSON.stringify(res))
					this.result = res.result;
					getPB08HByMtarea({
						mt_area: res.result
					}).then(res => {
						this.scanData=res.DicResult;
						console.log(res);
					});
				}
			});
		},
		scan1: function() {
			uni.scanCode({
				success: (res) => {
					GetPR02WIQTYByPtno({
						sc_rcno: res.result
					}).then(res => {
						this.scanData1=res.DicResult;
						console.log(res);
					});
					console.log(JSON.stringify(res))
					this.result1 = res.result
				}
			});
		}
	}
}
